//*************** Контейнеры ***************//

//Миксин - Для каждой точки останова определите минимальную ширину контейнера в медиа-запросе.
//
@mixin make-container-min-widths($min-widths: $container-min-widths, $breakpoints: $grid-breakpoints) {
  @each $breakpoint, $container-min-width in $min-widths {
    @include media-breakpoint-down($breakpoint, $breakpoints) {
      min-width: $container-min-width;
    }
  }
}


//Контейнер-обёртка
@if $enable-grid-classes {
  .wrapper-content {
    @include make-container(0);
    @include make-container-max-widths($container-content-max-widths);
    @include make-container-min-widths($container-content-min-widths);
  }
}


//Контейнер контента
@if $enable-grid-classes {
  .container.content {
    // @include make-container(0);
    @include make-container-min-widths($container-min-widths);
  }
}


//*************** header & footer ***************//


.header, .footer {
  .social {

    .social-icon {
      padding: 0;
      width: 24px;
      height: 24px;
      background: url(../images/social_network_icons.png) no-repeat;
      display: inline-block;
      margin-left: 7px;
      border-radius: 0;

      &:hover {
        background: url(../images/social_network_icons_active.png) no-repeat;
      }
    }

    .facebook,
    .facebook:hover {
      background-position: 0 0;
    }
    .vk,
    .vk:hover {
      background-position: -31px 0;
    }
    .ok,
    .ok:hover {
      background-position: -61px 0;
    }
    .youtube,
    .youtube:hover {
      background-position: -91px 0;
    }
    .instagram,
    .instagram:hover {
      background-position: -122px 0;
    }
  }
}


