//*************** Новые переменные ***************//


//Цвет фона контента, подвала
$bg-color-main-content: #ececec;
$bg-color-footer: #415f88;

$bg-carousel-active: #918f8f;
$bg-carousel-active-hover: rgba(145,143,143,0.85);
$bg-carousel: #525456;
$bg-carousel-hover: rgba(80,84,86,0.85);


//Карта цветов
$colors: ();

$colors: map-merge(
  (
    "bg-color-main-content": $bg-color-main-content,
    "bg-color-footer": $bg-color-footer
  ),
  $colors
);


//Цвета для кнопок

$theme-colors: ();
// stylelint-disable-next-line scss/dollar-variable-default
$theme-colors: map-merge(
  (
    "carousel-active": $bg-carousel-active,
    "carousel": $bg-carousel
  ),
  $theme-colors
);



